.switch-btn {
  display: none;
}

@media screen and (min-width: 768px) {
  .switch-btn {
    width: 40px;
    height: 20px;
    z-index: 0;
    cursor: pointer;
    background: #fff;
    border: .6px solid #4b48db;
    border-radius: 20px;
    margin: 0;
    padding: 0;
    transition-duration: .3s;
    display: inline-block;
    position: relative;
  }
}

.switch-btn:after {
  content: "";
  height: 16px;
  width: 16px;
  z-index: 1;
  background: #4b48db;
  border-radius: 20px;
  transition-duration: .3s;
  position: absolute;
  top: 1px;
  left: 0;
}

.switch-on:after {
  left: 21px;
}

.switch-btn_choice {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.switch-btn_choice_light {
  font-family: Poppins;
  font-weight: 400;
  font-style: Regular;
  align-items: left;
  color: #4b48db;
  margin-right: 13px;
  font-size: 18px;
  line-height: 89%;
}

.switch-btn_choice_dark {
  font-family: Poppins;
  font-weight: 400;
  font-style: Regular;
  align-items: left;
  color: #000;
  margin-left: 13px;
  font-size: 18px;
  line-height: 89%;
}

.search {
  position: relative;
}

.search-btn {
  width: 15px;
  height: 15px;
  cursor: pointer;
  border: none;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 65px;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  .search-btn {
    left: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .search-btn {
    left: 14px;
  }
}

.search-icon {
  width: 15px;
  height: 15px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.switch-btn_choice_icon {
  width: 19px;
  height: 19px;
  display: none;
}

.switch-btn_choice_dark-icon {
  margin-left: 11px;
}

.switch-btn_choice_light-icon {
  margin-right: 11px;
}

.switch-btn_choice_text {
  display: none;
}

@media screen and (min-width: 768px) {
