    margin-right: 11px;
    display: block;
  }

  .switch-btn_choice_text {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .switch-btn_choice_text {
    display: block;
  }

  .switch-btn_choice_icon {
    display: none;
  }
}

